import React, { useEffect, useState } from "react";
import "./DragAndDrop.css"; // Import styles for the component

const DragAndDropComponent = () => {
  const [csrfHeaders, setCsrfHeaders] = useState(null);
  const [droppedData, setDroppedData] = useState(null);
  const [isDropped, setIsDropped] = useState(false);

  useEffect(() => {
    // Fetch CSRF token and initialize drag-and-drop on mount
    loadDependencies();
  }, []);

  const loadDependencies = () => {
    console.log("[Dependencies] Loading WAFData and DataDragAndDrop...");
    window.require(
      ["DS/WAFData/WAFData", "DS/DataDragAndDrop/DataDragAndDrop"],
      (WAFData, DataDragAndDrop) => {
        console.log("[Dependencies] Modules loaded:", { WAFData, DataDragAndDrop });

        // Fetch CSRF token after modules are loaded
        fetchCsrfToken(WAFData, () => initializeDragAndDrop(DataDragAndDrop));
      }
    );
  };

  const fetchCsrfToken = (WAFData, callback) => {
    console.log("[CSRF] Fetching token...");
    const csrfURL =
      "https://oi000186152-us1-space.3dexperience.3ds.com/enovia/resources/v1/application/CSRF?tenant=OI000186152";

    if (WAFData.authenticatedRequest) {
      WAFData.authenticatedRequest(csrfURL, {
        method: "GET",
        type: "json",
        onComplete: (response) => {
          console.log("[CSRF] Token fetched:", response);
          const csrfToken = response.csrf.name;
          const csrfValue = response.csrf.value;
          const securityContextHeader = "SecurityContext";
          const securityContextValue =
            "ctx%3A%3AVPLMProjectLeader.BU-0000001.Rosemount%20Flow";

          const headers = {
            [csrfToken]: csrfValue,
            [securityContextHeader]: securityContextValue,
          };
          setCsrfHeaders(headers);
          console.log("[CSRF] Headers set:", headers);

          if (callback) callback();
        },
        onFailure: (error) => {
          console.error("[CSRF] Failed to fetch token:", error);
        },
      });
    } else {
      console.warn("[CSRF] WAFData is not available in this environment.");
    }
  };

  const initializeDragAndDrop = (DataDragAndDrop) => {
    console.log("[DragAndDrop] Initializing drag-and-drop...");
    const droppableContainer = document.querySelector(".droppable-container");

    if (!droppableContainer) {
      console.error("[DragAndDrop] Droppable container not found.");
      return;
    }

    DataDragAndDrop.droppable(droppableContainer, {
      drop: (data) => {
        console.log("[DragAndDrop] Drop event:", data);
        const parsedData = JSON.parse(data);
        handleDrop(parsedData.data.items);
      },
      enter: () => {
        console.log("[DragAndDrop] Drag Enter");
        droppableContainer.classList.add("drag-over");
      },
      leave: () => {
        console.log("[DragAndDrop] Drag Leave");
        droppableContainer.classList.remove("drag-over");
      },
    });
  };

  const handleDrop = (data) => {
    console.log("[Drop] Current CSRF headers:", csrfHeaders);
    if (!csrfHeaders) {
      console.error("[Drop] Missing CSRF headers. Cannot proceed.");
      return;
    }

    console.log("[Drop] Dropped data:", data);

    if (data.length > 1) {
      alert("Please drop only one object.");
      return;
    }
    setIsDropped(true);
    const objectID = data[0]?.objectId;
    fetchObjectDetails(objectID);
  };

  const fetchObjectDetails = (objectID) => {
    if (!objectID) {
      console.error("[Object Details] Missing object ID.");
      return;
    }

    const objectDetailsURL = `https://oi000186152-us1-space.3dexperience.3ds.com/enovia/resources/v1/modeler/dseng/dseng:EngItem/${objectID}?$mask=dsmveng:EngItemMask.Details`;

    window.require(["DS/WAFData/WAFData"], (WAFData) => {
      if (WAFData.authenticatedRequest) {
        WAFData.authenticatedRequest(objectDetailsURL, {
          method: "GET",
          headers: csrfHeaders,
          type: "json",
          onComplete: (response) => {
            console.log("[Object Details] Response:", response);
            setDroppedData(response);
          },
          onFailure: (error) => {
            console.error("[Object Details] Failed to fetch data:", error);
          },
        });
      } else {
        console.warn("[Object Details] WAFData is not available in this environment.");
      }
    });
  };

  return (
    <div className={`droppable-container ${isDropped ? "dropped-active" : ""}`}>
      <img
        src="https://thewhitechamaleon.github.io/RevisionFloat/Test/assets/images/drag-and-drop.png"
        alt="Drag and Drop"
      />
      <span>Drag and Drop</span>
      {droppedData && (
        <div className="dropped-data">
          <h4>Dropped Data:</h4>
          <pre>{JSON.stringify(droppedData, null, 2)}</pre>
        </div>
      )}
    </div>
  );
};

export default DragAndDropComponent;
