import React from 'react'
import DragAndDropComponent from '../components/DragAndDrop/DragAndDrop';
import { createColumnHelper } from "@tanstack/react-table";
import ReusableTable from '../components/Table/Table';
import CardComponent from '../components/Card/Card';

const RevisionFloat = () => {


  const data = React.useMemo(() => [
    { id: 1, name: 'John Doe', age: 28 },
    { id: 2, name: 'Jane Smith', age: 34 },
    { id: 3, name: 'Alice Johnson', age: 23 },
  ], []);

  const columns = [
    {
      accessorKey: "id",
      header: "ID",
    },
    {
      accessorKey: "name",
      header: "Name",
      editable: true, // Make this column editable
    },
    {
      accessorKey: "age",
      header: "Age",
      editable: true, // Make this column editable
    },
  ];

  const sampleData = [
    {
      title: 'Card Title 1',
      text: 'This is the first card text.',
      imageUrl: 'https://via.placeholder.com/150',
      footerText: 'Footer Text 1',
      buttonLabel: 'Click Me',
    }
  ];


  return (
    <div>
      <DragAndDropComponent />
      <ReusableTable data={data} columns={columns} editable />
      {sampleData.map((card, index) => (
        <CardComponent
          key={index}
          title={card.title}
          text={card.text}
          imageUrl={card.imageUrl}
          footerText={card.footerText}
          buttonLabel={card.buttonLabel}
          // onButtonClick={handleCardClick}
        />
      ))}
    </div>

  )
}

export default RevisionFloat